.loaderContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(215, 215, 212, 0.68);
    display: flex;
    justify-content: center;
    align-items: center;

    div {
        z-index: 1000;
        position: absolute;
    }
}


